import React, {useEffect, useState} from 'react'
import Notes from './Notes'
import Notification from "./Notification"
import Footer from "./services/Footer"
import noteService from './services/notes'
import loginService from "./services/login"

const App = () => {
    const [notes, setNotes] = useState([])
    const [newNote, setNewNote] = useState('')
    const [showAll, setShowAll] = useState(true)
    const [errorMessage, setErrorMessage] = useState(null)
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [user, setUser] = useState(null)

    const addNote = (event) => {
        event.preventDefault()
        const noteObject = {
            content: newNote,
            important: Math.random() < 0.5,
        }

        noteService.create(noteObject).then(returnedNote => {
            setNotes(notes.concat(returnedNote))
            setNewNote('')
        })
    }

    const handleNoteChange = (event) => {
        console.log(event.target.value)
        setNewNote(event.target.value)
    }

    const notesToShow = showAll
        ? notes
        : notes.filter(note => note.important)

    useEffect(() => {
        noteService.getAll().then(initialNotes => {
            setNotes(initialNotes)
        })
    }, [])

    useEffect(() => {
        const loggedUserJSON = window.localStorage.getItem('loggedNoteappUser')
        if (loggedUserJSON) {
            const user = JSON.parse(loggedUserJSON)
            setUser(user)
            noteService.setToken(user.token)
        }
    }, [])

    // console.log('render', notes.length, 'notes');

    const toggleImportanceOf = (note) => () => {
        const changedNote = {...note, important: !note.important}

        noteService.update(note.id, changedNote)
            .then(returnedNote => {
                setNotes(notes.map(current => note.id !== current.id ? current : returnedNote))
            })
            .catch(error => {
                // setErrorMessage(`Note '${note.content}' was already removed from server`)
                setErrorMessage('401 Unauthorized')
                console.log(error, '-----front end')
                setTimeout(() => {
                    setErrorMessage(null)
                }, 5000)
                // setNotes(notes.filter(n => n.id !== note.id))
            })
    }

    const deleteNotes = (event) => {
        if (window.confirm("Delete " + event.target.value + "?")) {
            const index = notes.findIndex(current => current.content === event.target.value)
            const id = notes[index].id
            noteService.remove(id)
                .then(() => {
                    setNotes(notes.filter(current => current.id !== id))
                })
                .catch(error => {
                    // setErrorMessage(`Note '${event.target.value}' was already removed from server`)
                    setErrorMessage('401 Unauthorized')
                    console.log(error, '------front end')
                    setTimeout(() => {
                        setErrorMessage(null)
                    }, 5000)
                })
        }
    }

    const handleLogin = async (event) => {
        event.preventDefault()

        try {
            const user = await loginService.login({
                username, password,
            })
            window.localStorage.setItem(
                'loggedNoteappUser', JSON.stringify(user)
            )
            noteService.setToken(user.token)
            setUser(user)
            setUsername('')
            setPassword('')
        } catch (exception) {
            setErrorMessage('Wrong credentials')
            setTimeout(() => {
                setErrorMessage(null)
            }, 5000)
        }
    }

    const handleLogout = () => {
        window.localStorage.removeItem('loggedNoteappUser')
        noteService.setToken('')
        setUser(null)
    }

    const loginForm = () => (
        <form onSubmit={handleLogin}>
            <div>
                username
                <input type="text" value={username} name="Username" onChange={({target}) => setUsername(target.value)}/>
            </div>

            <div>
                password
                <input type="password" value={password} name="Password"
                       onChange={({target}) => setPassword(target.value)}/>
            </div>

            <button type="submit">login</button>
        </form>
    )

    const noteForm = () => (
        <form onSubmit={addNote}>
            <input value={newNote} onChange={handleNoteChange}/>
            <button type="submit">save</button>
        </form>
    )

    return (
        <div>
            <h1>Notes</h1>
            <Notification message={errorMessage}/>

            {user === null ?
                loginForm() :
                <div>
                    <p>{user.name} logged-in</p>
                    {noteForm()}
                    <button onClick={handleLogout}>
                        log out
                    </button>
                </div>
            }

            <div>
                <button onClick={() => setShowAll(!showAll)}>
                    show {showAll ? 'important' : 'all'}
                </button>
            </div>

            <ul>
                {notesToShow.map(note => <Notes
                    key={note.id}
                    note={note}
                    toggleImportance={toggleImportanceOf(note)}
                    deleteFunc={deleteNotes}/>)}
            </ul>

            <Footer/>
        </div>
    )
}

export default App;
